@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: 'Oswald', sans-serif, 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Courier New',
    monospace;
}

*{
    box-sizing: border-box;

}

body{
    font-size: 70px;
    color: #fff;  
    background: #00ff00;  
}


table{
    margin-left: 100px;
    margin-top: 100px;
    border-collapse: collapse;
}

table tbody{
    background-color: blue;
    border: 4px solid #fff;
}

.p{
    color: #00ff00;
    font-size: 50px;
}

.puntuacion{
    font-size: 50px;
    -webkit-text-stroke: 2px black;
    color: transparent; 
    color: #fff;
    text-align: left;
}


.saque{
    font-size: 50px;
    width: 85px;
    margin-top: 12px;
    border-bottom: 4px solid #fff;
}

.parejas{
    padding-left: 30px;
    margin-right: 0px;
    border-bottom: 4px solid #fff;
    width: auto;
    min-height: 86px;
    min-width: 300px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.puntos{
    width: 100px;
    padding: 0px 10px;
    background: #fff;
    color: blue;
    box-shadow: inset 0 0 0 2px blue;
}

.oro{
    width: 100px;
    padding: 0px 10px;
    background: yellow;
    color: blue;
    box-shadow: inset 0 0 0 2px blue;
}

.tbr{
    width: 100px;
    padding: 0px 10px;
    background: red;
    color: #fff;
    box-shadow: inset 0 0 0 2px #fff;
}

.set{
    width: 75px;
    border-bottom: 4px solid #fff;
    border-left: 4px solid #fff;
    border-right: 4px solid #fff;
}

.nombres{
    display: inline-block;
}
